*, html {
  font-family: Montserrat, sans-serif;
}

body {
  margin: 0;
  padding: 0;
  background: linear-gradient(to right, rgb(197, 52, 52), rgb(76, 0, 255));
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.card {
    border-radius: 20px;
    box-shadow: 0 2px 5px 3px #151515;
    cursor: pointer;
}

h2 {
    letter-spacing: 2px;
}
p {
    letter-spacing: 1px;
}

.search {
    margin-bottom: 20px;
    border-radius: 10px;
    box-sizing: border-box;
}

.search::placeholder {
    text-transform: capitalize;
}
h1 {
    font-family: Kaushan Script, sans-serif;
    text-transform: uppercase;
    letter-spacing: 6px;
    margin: 20px;
}

